.card {
    background: #fff;
    border-radius: 2px;
    height: 180px;
    margin: 1rem;
    position: relative;
    width: 180px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    
  }
  .card > .img-container > img {
    width: 100%;
    background-size: cover;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  
  
.wrapper {
    width: 900px;
    padding-right: 200px;
    padding-left: 200px;
    padding-top: 50px;
    padding-bottom: 50px;
    background: #ECEFF1;
    height: 70%;
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
    justify-content: center;
    overflow: auto;
    margin: 0 auto;
  }
  .image-container {
    width: 160px;
    height: 160px;
  }
  
.title {
    width: 100%;
    text-align: center;
    font-size: 3rem;
    text-decoration: underline;
    color: #2A77AE;
    margin-bottom: 50px;
  }
nav {
    width: 100%;
    color: rgb(241, 231, 231);
    margin-bottom: 50px;
    background-color: rgb(17, 10, 10);
    height: 80px;
    font-family: Arial, Helvetica, sans-serif;
    position: fixed;
    top: 0;
    z-index: 3;
    font-size: 30px;
    
  }
ul {
  display: flex; 
  flex-direction: row;
  justify-content: space-evenly;
  list-style-type: none;
}
#score, #topscore {
  float: right;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

